.nav {
  &__list {
    display: flex;
    justify-content: center;

    &--mobile {
      @include tablet {
        position: absolute;
        flex-direction: column;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__item {
    a {
      font: var(--middle-font);
      color: var(--color-blueJeans);
      cursor: pointer;
      border-bottom: 2px solid transparent;
      transition: border-bottom 0.3s ease-in;

      &:hover {
        border-bottom: 2px solid var(--color-blueJeans);
        transition: border-bottom 0.3s ease-in;
      }
    }

    &:not(:last-child) {
      margin-right: 35px;

      @include tablet {
        margin-right: 0;
      }

      @include mobile {
        margin-bottom: 10px;
      }
    }
  }
}


.nav__list .social {
  margin-top: 30px;
}
