@import "mixins";

.footer {
  padding: 100px 0;

  @include small-tablet {
    padding: 40px 0;
  }

  &__container {
    display: flex;

    @include small-tablet {
      flex-direction: column-reverse;
    }
  }

  &__contacts {
    width: 35%;

    @include small-tablet {
      width: 100%;
      text-align: center;
      margin: auto;
      margin-top: 20px;
    }
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    width: 65%;
    padding-top: 65px;

    @include small-tablet {
      width: 100%;
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__list {
    &--item {
      text-shadow: 0 0 10px rgb(0 0 0 / 50%);
      &:not(:last-child) {
        margin-bottom: 30px;
      }

      a {
        font-weight: 600;
        font-size: 18px;
        color: var(--color-white);
        border-bottom: 2px solid transparent;

        &:hover {
          border-bottom: 2px solid var(--color-white);
          transition: border-bottom 0.3s ease-in;
        }
      }
    }
  }

  &__list {
    @include mobile {
      text-align: center;
      width: 100%;
    }
  }
}

.contacts {
  &__image {
    margin-bottom: 24px;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 290px;
    text-shadow: 0 0 10px rgb(0 0 0 / 50%);

    @include small-tablet {
      margin: auto;
    }

    &:not(:last-child) {
      margin-bottom: 12px;
    }

    a {
      font-size: 21px;
      line-height: 25px;
      font-weight: 600;
      color: var(--color-white);
    }
  }

  &__icon {
    width: 46px;
    height: 46px;
    border-radius: 100%;
    background-color: var(--color-blueJeans);
    position: relative;
    margin-right: 13px;
    flex-shrink: 0 ;


    img {
      width: 22px;
      height: 22px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &--whatsapp {
      img {
        width: 35px;
        height: 35px;
      }
    }

    &--address {
      img {
        width: 24px;
        height: 34px;
      }
    }
  }

  &__copyright {
    font-weight: 500;
    font-size: 18px;
    color: var(--color-white);
    margin-top: 50px;
  }
}

.social {
  @include mobile {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 20px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-cyanBlue);
    border-radius: 23px;
    color: var(--color-white);
    font-size: 21px;
    font-weight: 600;
    padding: 5px 5px 5px 15px;
    width: 269px;
    opacity: 1;
    transition: opacity 0.3 ease-in-out;

    &:hover {
      opacity: 0.8;
      transition: opacity 0.3 ease-in-out;
    }

    @include mobile {
      font-size: 15px;
      width: 57px;
      flex-direction: row-reverse;
      border-radius: 100%;
      margin-bottom: 0 !important;

      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    span {
      @include mobile {
        display: none;
      }
    }
  }

  &__image {
    width: 46px;
    height: 46px;
    border-radius: 100%;
    background-color: var(--color-blueJeans);
    position: relative;
    flex-shrink: 0;

    img {
      width: 28px;
      height: 28px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.arrow__up {
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 7%;
  right: 4%;
  cursor: pointer;
  transform: translateY(0);
  transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;


  svg {
    width: 100%;
    height: 100%;
    fill: var(--color-imperialRed);
  }

  &:hover {
    transform: translateY(-5px);
    transition: transform 0.3s ease-in-out;
  }
}

.visible {
  opacity: 1;
  visibility: visible;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;

}
