.catalog {
  padding: 80px 0;
  &__title {
    font: var(--big-font);
    color: var(--color-black);
    font-weight: 600;
    margin-bottom: 30px;
  }

  &__product {
    padding-top: 109px;

    @include mobile {
      padding-top: 10px;
    }
  }

  &__list {
    justify-content: space-between;

    @include tablet {
      flex-wrap: wrap;
    }

  }

  &__item {
    background-color: var(--color-white);
    border: 1px solid #000000;
    width: 274px;
    border-radius: 10px;
    padding: 51px 11px 11px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @include tablet {
      width: 46%;
      margin-bottom: 2%;

      &:nth-child(2n){
        margin-right: 2%;
      }
    }

    @include mobile {
      width: 100%;
      padding: 7px 2px 11px;
    }
  }

  &__image {
    text-align: center;

    img {
      flex-shrink: 0;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 17px 27px;

    @include mobile {
      display: block;
      font-size: 25px;
      text-align: center;
    }

    span {

    }
    font-size: 36px;
    color: var(--color-black);

    &--item {
      display: flex;
      align-items: center;
      input {
        width: 40px;
        height: 40px;
        border: 1px solid var(--color-black);
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        border-radius: 9px;
        text-align: center;
        font-size: 17px;
        font-weight: 600;
        margin: 0 10px;
      }

      @include mobile {
        display: block;
        margin-top: 10px;
      }
    }
  }

  &__descr {
    font-size: 33px;
    font-weight: 700;
    margin-left: 27px;

    @include mobile {
      margin-left: 0;
      font-size: 24px;
      text-align: center;
    }
  }

  &__btn {
    border-radius: 10px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 22px;
    padding:  8px 55px;
    text-align: center;

    @include mobile {
      font-size: 14px;
      padding: 8px 32px;
      max-width: 200px;
      margin: 0 auto;
    }
  }
}


.category {
  display: flex;
  align-items: center;

  @include small-tablet {
    overflow-x: auto;
  }

  &__btn {
    background: linear-gradient(93.58deg, rgba(255, 255, 255, 0) -0.02%, rgba(255, 255, 255, 0.4) 99.98%), #F2F2F2;
    border: 1px solid #000000;
    box-shadow: 6px 10px 25px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 14px 30px;
    font-weight: 700;
    color: var(--color-black);
    text-transform: uppercase;
    margin-right: 24px;

    @include mobile {
      margin-bottom: 20px;
      padding: 14px;
    }

    &--active {
      background-color: var(--color-blueJeans);
      color: var(--color-white);
    }
  }
}


.hide {
  display: none;
}

.open {
  display: flex;
}
