@import "mixins";
// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1235px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-black: #000000;
  --color-blueJeans: #50BEFF;
  --color-imperialRed: #EC1F3A;
  --color-cyanBlue: #3287BF;
  --color-bg: #e2f2f2;

  //fonts
  --middle-font: 600 21px/26px var(--font-family);
  --big-font: 700 57px/70px var(--font-family);
}


@include tablet {
  :root {
    --big-font: 700 47px/70px var(--font-family);
  }
}


@include mobile {
  :root {
    --big-font: 700 32px/32px var(--font-family);
  }
}
