@import "mixins";

.description {
  &__title {
    font: var(--big-font);
    max-width: 900px;
    margin-bottom: 30px;
  }

  &__descr {
    font-size: 24px;

    @include mobile {
      font-size: 20px;
    }
  }

  &__subtitle {
    font: var(--big-font);
    padding-top: 20px;
    padding-bottom: 70px;

    @include small-tablet {
      padding-bottom: 40px;
    }
  }

  &__video {
    text-align: center;

    @include small-tablet {
      iframe {
        width: 600px;
        height: 420px;
      }
    }

    @include mobile{
      iframe {
        width: calc(100% - 20px);
        height: 250px;
      }
    }
  }
}
