.overlay {
  position: fixed;
  z-index: 101;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(110, 156, 159, 0.95);
  visibility: hidden;

  &--visible {
    visibility: visible;
  }
}


.popup {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--color-blueJeans);
  max-width: 600px;
  border-radius: 6px;
  padding: 50px 50px 60px;
  cursor: auto;

  @include mobile {
    padding: 50px 20px;
  }


  &__wrapper {
    position: fixed;
    z-index: 111;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    visibility: hidden;
    cursor: pointer;

    &--visible {
      visibility: visible;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 25px;
    margin-bottom: 35px;
    margin-bottom: 15px;
  }

  &__result {
    text-align: center;
  }

  &__btn {
    font-weight: 400;
    font-size: 17px;
    padding: 22px 50px;
    background-color: transparent;
    border: 1px solid var(--color--cyanD);
    color: var(--color-black);

    &:hover {
      border: 1px solid transparent;
      background-color: var(--color--cyanD);
      color: var(--color-white);
    }
  }
}

.popup-form {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__text {
    font-size: 30px;
    font-weight: 500;
  }

  &__label {
    width: 100%;
    margin-bottom: 15px;

  }

  &__input {
    padding: 10px 30px;
    border-radius: 10px;
    border: 1px solid var(--color-blueJeans);

  }

  &__submit {
    font-weight: 400;
    font-size: 17px;
    max-width: 300px;
    padding: 10px 30px;
    border-radius: 10px;

    &:hover {
      background-color: var(--color-white);
      color: var(--color-black);
    }
  }
}

.close_button
{
  position: absolute;
  height: 56px;
  width: 56px;
  right: 0;
  top: 0;
  background: url('../../img/close.png') no-repeat 20px 20px;
  cursor: pointer;
}
