@import "mixins";
.hero {
  background-image: url("../../img/hero__bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 780px;

  &__container {
    padding: 325px 100px 225px;
    position: relative;

    @include tablet {
      padding: 126px 46px 331px;
    }

    @include small-tablet {
      padding: 126px 46px 231px;
    }

    @include mobile {
      padding: 124px 27px 150px;
    }
  }

  &__title {
    font: var(--big-font);
    text-transform: uppercase;
    color: var(--color-white);
    text-shadow: 0 0 10px rgba(0,0,0,0.5);
    max-width: 712px;
    margin-bottom: 100px;


    @include  tablet {
      max-width: 650px;
    }

    @include mobile {
      margin-bottom: 225px;
    }
  }

  &__form {

    display: flex;
    align-items: center;
    //max-width: 523px;

    @include small-tablet {
      z-index: 99;
      position: relative;
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__input {
    height: 69px;
    background: rgba(164, 234, 255, 0.5);
    backdrop-filter: blur(11px);
    border-radius: 10px;
    border: none;
    width: 300px;
    color: rgba(0, 0, 0, 0.6);
    font: var(- --middle-font);
    font-size: 26px;
    padding-left: 25px;

    @include mobile {
      margin-bottom: 10px;
      width: 100%;
    }
  }

  &__btn {
    position: relative;
    left: -15px;
    border-radius: 10px;
    width: 238px;
    height: 69px;
    padding: 19px 36px 19px 45px;
    font: var(- --middle-font);
    font-size: 26px;
    font-weight: 700;
    text-transform: uppercase;

    @include small-tablet {
      font-size: 22px;
    }

    @include mobile{
      left: 0;
      width: 300px;
    }
  }

  &__contacts {
    position: absolute;
    right: 0;
    top: 197px;

    @include mobile {
      right: -205px;
      top: 228px;

      &:hover {
        right:0;
      }
    }
  }

  &__list {
    width: 269px;
  }

  &__image {
    width: 48px;
    height: 48px;
  }

  &__item {
    padding: 2px 8px;
    background: var(--color-cyanBlue);
    border-radius: 10px;
    margin-bottom: 9px;

    @include mobile {
      flex-direction: row-reverse;
    }

    a {
      font: var(--middle-font);
      color: var(--color-white);
    }
  }

  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile {
      flex-direction: row-reverse;
    }
  }

  &__bottle {
    position: absolute;
    right: 0;
    bottom: 0;

    @include tablet {
      right: -81px;
      width: 400px;
    }

    @include mobile {
      display: none;
    }
  }
}

