@import "../mixins";

.calculate {
  padding: 100px 0;

  @include small-tablet {
    padding: 40px 0;
  }

  &--bottom__item {
    display: flex;
    align-items: center;

    @include tablet {
      margin-bottom: 20px;
    }

    img {
      flex-shrink: 0;
    }

    input {
      text-align: center;
      font-size: 24px;
      font-weight: 600;
      width: 35px;
      height: 35px;
      border: none;
    }

    p {
      margin-right: 31px;

      @include mobile {
        margin-right: 10px;
      }
    }

    img {
      margin-right: 20px;
    }
  }

  &__title {
    font: var(--big-font);
    margin-bottom: 25px;
  }

  &__block {
    margin: 10px 128px;
    background-color: var(--color-white);
    padding: 60px 82px 92px 53px;
    border-radius: 27px;

    @include tablet {
      margin: 10px;
    }

    @include mobile {
      padding: 36px 15px;
    }
  }

  &__list {
    display: flex;
    align-items: start;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__item {
    width: 25%;

    @include tablet {
      width: 50%;
      align-items: center;
      text-align: center;
      padding-bottom: 30px;
      margin-bottom: 30px;
    }

    @include mobile {
      width: 100%;
      padding-top: 30px;
      margin-bottom: 0;
    }

    &:nth-child(2n) {
      .calculate__text {
        margin-bottom: 41px;
      }
    }
    span {
      text-transform: uppercase;
      font-weight: 600;
    }

    @include mobile {
      border-top: 2px solid #a7dfff;
    }

    &:not(:last-child) {
      border-right: 2px solid #a7dfff;

      @include mobile {
        border-right: none;
        border-top: 2px solid #a7dfff;
      }
    }

    &--calc {
      padding: 0 45px;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;

      @include mobile {
        padding-top: 30px;
        padding-bottom: 30px;
      }
    }

    &--prep {
      padding-left: 50px;

      @include tablet {
        padding-left: 0;
      };
    }
  }

  &__text {
    font-weight: 400;
    font-size: 17px;
    text-transform: uppercase;
    margin-bottom: 21px;
  }

  &__btn {
    border: 1px solid #006FCC;
    border-radius: 10px;
    padding: 8px 7px;
    width: 120px;
    text-transform: uppercase;
    font-size: 13px;
    color: black;
    font-weight: 600;
    display: block;

    @include tablet {
      display: inline-block;
    }

    &:not(:last-child) {
      margin-bottom: 14px;
    }

    &--active {
      background-color: var(--color-blueJeans);
      color: var(--color-white);
      border: 1px solid transparent;
    }

    &:hover {
      background-color: var(--color-blueJeans);
      color: var(--color-white);
      border: 1px solid transparent;
    }
  }

  &__fields {
    display: flex;
    width: 100%;
    justify-content: space-around;
    margin-bottom: 18px;

    input {
      width: 40px;
      height: 40px;
      border: 1px solid var(--color-blueJeans);
      box-sizing: border-box;
      border-radius: 9px;
      text-align: center;
      font-size: 17px;
      font-weight: 600;
    }

    button {
      font-size: 22px;
      color: #8399A9;
      font-weight: 600;
      width: 100%;
    }
  }

  &__bottom {
    margin-top: 72px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include tablet {
      flex-wrap: wrap;
      margin-top: 20px;
    }
  }

  &__submit {
    padding: 13px 58px;
    border-radius: 7px;
    text-transform: uppercase;
    font-weight: 700;

    @include tablet {
      width: 100%;
    }
  }
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}



.calculate__radio--delivery input[type=radio], .calculate__radio--preparation input[type=radio] {
  display: none;
}


.calculate__radio--delivery label, .calculate__radio--preparation label {
  text-align: center;
  cursor: pointer ;
  display: inline-block;
  border: 1px solid #006FCC;
  border-radius: 10px;
  padding: 8px 7px;
  width: 120px;
  text-transform: uppercase;
  font-size: 13px;
  color: black;
  font-weight: 600;

  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

/* Checked */
.calculate__radio--delivery input[type=radio]:checked + label,
.calculate__radio--preparation input[type=radio]:checked + label{
  background-color: var(--color-blueJeans);
  color: var(--color-white);
  border: 1px solid transparent;
}

/* Hover */
.calculate__radio--delivery label:hover,
.calculate__radio--preparation label:hover{
  background-color: var(--color-blueJeans);
  color: var(--color-white);
  border: 1px solid transparent;
}
