.benefits {

  padding: 50px 0;

  @include mobile {
    padding: 0;
  }

  &__title {
    max-width: 718px;
    font: var(--big-font);
    color: var(--color-imperialRed);

    span {
      color: var(--color-black);
      font-weight: 600;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__reasons {
    text-align: center;
  }
  &__list {
    display: flex;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__item {
    padding-right: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include small-tablet {
    padding-right: 0;
      margin-bottom: 20px;
    }
  }
}
