@import "mixins";

.header {
  background: rgba(255, 255, 255, 0.6);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    //flex-shrink: 0;
    @include mobile {
      margin-right: 5px;
    }
  }

  &__nav {
    @include tablet {
      display: none;
    }
  }

  &__mobile {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    background-color: #312b28;
    padding: 50px;
    text-align: center;

    &--active {
      display: block;
    }
  }
}


.burger {
  @include burger;
  z-index: 1000;
  color: var(--color-blueJeans);
  display: none;

  @include tablet {
    display: block;
  }
}
